import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material';
import { QuestionService } from '../../services/question/question.service';

@Component({
  selector: 'app-hint',
  templateUrl: './hint.component.html',
  styleUrls: ['./hint.component.scss']
})
export class HintComponent implements OnInit {

  private hint: string;

  private page: {
    width: number,
    height: number
  };

  private editor: {
    text: string,
    mode: string,
    theme: string,
    options: any,
    readOnly: boolean,
    style: any
  };

  constructor(private dialogRef: MatDialogRef<HintComponent>, private questionService: QuestionService) {
    this.hint = "";

    this.page = {
      width: window.innerWidth - 400,
      height: window.innerHeight - 200
    };

    this.editor = {
      text: null,
      mode: "javascript",
      theme: "twilight",
      options: {},
      readOnly: true,
      style: {
        "height.px": this.page.height,
        "width.px": this.page.width
      }
    };
  }

  ngOnInit() {
  }

  private requestHint = function() {
    this.questionService.requestAnswer(this.hint, (code) => {
      this.editor.text = code;
      console.log("this.editor.text", this.editor.text);
    });
  };

  private close = function() {
    this.dialogRef.close();
  };

}



