let checkPalindromeCode =
`var JS_TEST = (function() {
    var app = {};
    let callback;
    app.run = function(cb) {
        callback = cb;
        // The answer is here!
        return checkPalindrome("hlbeeykoqqqqokyeeblh");
    };

    function checkPalindrome(inputString) {
        var size = inputString.length
        var result = true
        for(var i = 0; i < size/2; i++){
            if(inputString[i] != inputString[size - i - 1]) {
                result = false
                break
            }
        }
        return result
    }

    return app;
})();

return JS_TEST.run(cb);`;

let isLuckyCode =
`var JS_TEST = (function() {
    var app = {};
    let callback;
    app.run = function(cb) {
        callback = cb;
        // The answer is here!
        return isLucky(134008);
    };

    function isLucky(n) {
        var nStr = "" + n
        var firstHalf = 0
        var secondHalf = 0
        for(var i = 0; i < nStr.length / 2; i ++) {
            firstHalf += Number(nStr[i])
            secondHalf += Number(nStr[nStr.length / 2 + i])
        }
        return firstHalf == secondHalf
    }

    return app;
})();

return JS_TEST.run(cb);`;

