let adjacentElementsProductCode =
`var JS_TEST = (function() {
    var app = {};
    let callback;
    app.run = function(cb) {
        callback = cb;
        // The answer is here!
        return adjacentElementsProduct([4, 1, 2, 3, 1, 5]);
    };

    function adjacentElementsProduct(inputArray) {
        var max = -Infinity
        for (var i = 0; i < inputArray.length - 1; i ++) {
            var prod = (inputArray[i] * inputArray[i+1])
            if (max < prod) {
                max = prod
            }
        }
        return max
    }

    return app;
})();

return JS_TEST.run(cb);`;

let almostIncreasingSequenceCode =
`var JS_TEST = (function() {
    var app = {};
    let callback;
    app.run = function(cb) {
        callback = cb;
        // The answer is here!
        return almostIncreasingSequence([1, 2, 3, 4, 99, 5, 6]);
    };

    function almostIncreasingSequence(sequence) {
        var removeCount = 0
        for(var i = 0; i < sequence.length - 1; i ++) {
            if(sequence[i] >= sequence[i + 1]) {
                removeCount += 1
                var preCheck = true
                var nexCheck = true
                if(sequence[i - 1]) {
                    if(sequence[i - 1] >= sequence[i + 1]) {
                        preCheck = false
                    }
                }
                if(sequence[i + 2]) {
                    if(sequence[i] >= sequence[i + 2]) {
                        nexCheck = false
                    }
                }
                if(!preCheck && !nexCheck) {
                    removeCount += 1
                }
            }
            if(removeCount > 1) {
                return false
            }
        }
        return true
    }

    return app;
})();

return JS_TEST.run(cb);`;

let matrixElementsSumCode =
`var JS_TEST = (function() {
    var app = {};
    let callback;
    app.run = function(cb) {
        callback = cb;
        // The answer is here!
        return matrixElementsSum([[1,1,1,0],[0,5,0,1],[2,1,3,10]]);
    };

    function matrixElementsSum(matrix) {
        var result = 0
        for(var o in matrix) {
            for(var p in matrix[o]){
                if(matrix[o][p] == 0) {
                    for(var i = matrix.length - 1; i > Number(o); i --) {
                        matrix[i][p] = 0
                    }
                } else {
                    result += matrix[o][p]
                }
            }
        }
        return result
    }

    return app;
})();

return JS_TEST.run(cb);`;

let reverseParenthesesCode =
`var JS_TEST = (function() {
    var app = {};
    let callback;
    app.run = function(cb) {
        callback = cb;
        // The answer is here!
        return reverseParentheses("a(bcdefghijkl(mno)p)q");
    };

    function reverseParentheses(s) {
        while(s.indexOf("(") != -1) {
            var openIdx = -1
            var closeIdx = -1
            var check = 0
            for(var o in s) {
                var cha = s[o]
                if(cha == "(") {
                    if(openIdx == -1) {
                        openIdx = Number(o)
                    }
                    check = check + 1
                }
                if(cha == ")") {
                    check = check - 1
                    if(check == 0) {
                        closeIdx = Number(o)
                        break;
                    }
                }
            }
            var content = s.substring(openIdx + 1, closeIdx);
            content = content.split("").reverse().join("");
            content = content.replace(/\\(/g, "_");
            content = content.replace(/\\)/g, "(");
            content = content.replace(/_/g, ")");
            var beforeContent = s.substring(0, openIdx);
            var afterContent = s.substring(closeIdx + 1);
            s = beforeContent + content + afterContent;
        }
        return s;
    }

    return app;
})();

return JS_TEST.run(cb);`;

let stringsRearrangementCode =
`var JS_TEST = (function() {
    var app = {};
    let callback;
    app.run = function(cb) {
        callback = cb;
        // The answer is here!
        return stringsRearrangement(["abc", "abx", "axx", "abx", "abc"]);
    };

    function stringsRearrangement(inputArray) {
        var b = permutations(inputArray);
        var e = [];

        for (var c = 0; c < b.length; c++) {
            e[c] = true;
            for (var d = 1; d < b[c].length; d++) {
                if (!areDiffBy1Char(b[c][d - 1], b[c][d])) {
                    e[c] = false;
                    continue;
                }
            }
            if (e[c] === true) {
                return true
            }
        }
        return false;
    }

    function permutations(arr) {
        var arrangements = [];
        for (var i = 0; i < arr.length; i++) {
            var other_than_i = permutations(arr.slice(0, i).concat(arr.slice(i + 1)));
            if(other_than_i.length === 0) {
                arrangements.push([arr[i]]);
            }
            else {
                for(var j = 0; j < other_than_i.length; j++) {
                    arrangements.push([arr[i]].concat(other_than_i[j]));
                }
            }
        }
        return arrangements;
    }

    function areDiffBy1Char(x, y) {
        var w = 0;
        for(var z = 0; z < x.length; z++) {
            if(x.charAt(z) === y.charAt(z)) {
                w++;
            }
        }
        if(w === y.length - 1) {
            return true;
        }
        return false;
    }

    return app;
})();

return JS_TEST.run(cb);`;

