import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { BrowserXhr, HttpModule } from '@angular/http';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { RouterModule, Routes } from '@angular/router';

import 'hammerjs';

import { MatSidenavModule, MatToolbarModule, MatButtonModule, MatIconModule, MatListModule, MatGridListModule, MatDialogModule, MatInputModule } from '@angular/material';
import { AceEditorModule } from 'ng2-ace-editor';

import { QuestionService } from './services/question/question.service'

import { AppComponent } from './app.component';
import { JsTestPageComponent } from './pages/js-test-page/js-test-page.component';
import { QuestionComponent } from './components/question/question.component';
import { TestComponent } from './components/test/test.component';
import { HintComponent } from './components/hint/hint.component';

const appRoutes: Routes = [{
  path: 'js_test',
  component: JsTestPageComponent
}, {
  path: 'js_test/:title',
  component: JsTestPageComponent
}, {
  path: '**',
  component: JsTestPageComponent
}];

@NgModule({
  declarations: [
    AppComponent,
    JsTestPageComponent,
    QuestionComponent,
    TestComponent,
    HintComponent
  ],
  entryComponents: [
    HintComponent
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    FormsModule,
    ReactiveFormsModule,
    HttpModule,
    RouterModule.forRoot(appRoutes, {
      // enableTracing: true
    }),
    AceEditorModule,
    MatSidenavModule,
    MatToolbarModule,
    MatButtonModule,
    MatIconModule,
    MatListModule,
    MatGridListModule,
    MatDialogModule,
    MatInputModule
  ],
  providers: [
    QuestionService
  ],
  bootstrap: [
    AppComponent
  ]
})
export class AppModule { }



