import { Component } from '@angular/core';
import { QuestionService } from './services/question/question.service';
import { MatDialog } from '@angular/material';
import { HintComponent } from './components/hint/hint.component';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {
  private ui: {
    window: {
      height: number
    }
  };

  private menu: Array<{
    title: string,
    icon: string,
    link: string,
    active: boolean
  }>;

  private title: string;
  private hintHit: number;

  constructor(private questionServices: QuestionService, public dialog: MatDialog) {
    this.ui = {
      window: {
        height: window.innerHeight
      }
    };

    this.menu = [{
      title: "JS Test",
      icon: "code",
      link: "/js_test",
      active: false
    }];

    this.hintHit = 0;

    this.loadQuestions();
  }

  private loadQuestions = function() {
    this.questionServices.requestQuestionList((list) => {
      for(let o in list) {
        let q = list[o];
        this.menu.push({
          title: q.title + " : " + q.difficulty,
          icon: "code",
          link: "/js_test/" + q.title,
          active: false
        });
      }
    });
  };

  private activeMenuItem = function(idx: number) {
    for(var o in this.menu) {
      this.menu[o].active = false;
      if(idx == Number(o)) {
        let item = this.menu[o];
        item.active = true;
        this.title = item.title;
      }
    }
  };

  private openHint = function() {
    this.hintHit += 1;
    if(this.hintHit == 7) {
      this.hintHit = 0;
      console.log("openHint");
      let dialogRef = this.dialog.open(HintComponent, { });
      dialogRef.afterClosed().subscribe(result => {
        console.log('The dialog was closed');
      });
    }
  };
}



