import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { saveAs } from 'file-saver';
import { Question } from '../../modules/question';
import { Codes } from '../../resources/codes';
import { QuestionService } from '../../services/question/question.service';

@Component({
  selector: 'app-js-test-page',
  templateUrl: './js-test-page.component.html',
  styleUrls: ['./js-test-page.component.scss']
})
export class JsTestPageComponent implements OnInit {

  private sub: any;

  private page: {
    height: number,
    storeKey: string
  };

  private editor: {
    text: string,
    mode: string,
    theme: string,
    options: any,
    readOnly: boolean,
    autoUpdateContent: boolean,
    durationBeforeCallback: number,
    style: any
  };

  private log: {
    text: string,
    result: string,
    duration: number,
    style: any
  };

  private question: Question;

  constructor(private route: ActivatedRoute, private questionService: QuestionService) {
    this.page = {
      height: window.innerHeight - 56,
      storeKey: "JS_TEST_TMP"
    };

    this.editor = {
      text: Codes.DEFAULT,
      mode: "javascript",
      theme: "twilight",
      options: {},
      readOnly: false,
      autoUpdateContent: true,
      durationBeforeCallback: 1000,
      style: {
        "min-height.px": this.page.height
      }
    };

    this.loadCode((code) => {
      this.editor.text = code;
    });

    this.log = {
      text: "",
      result: "",
      duration: 0,
      style: {
        "max-height.px": this.page.height - 88
      }
    };

    this.question = null;
  }

  ngOnInit() {
    this.sub = this.route.params.subscribe((params) => {
      this.loadQuestion(params.title);
    });
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

  private loadQuestion = function(title: string) {
    if(title) {
      this.questionService.requestQuestion(title, (data) => {
        this.question = data;
      })
    }
  };

  private saveCode = function(code) {
    localStorage.setItem(this.page.storeKey, code);
  };

  private loadCode = function(success) {
    let code = localStorage.getItem(this.page.storeKey);
    if(code) {
      success(code);
    }
  };

  private clearCode = function() {
    localStorage.removeItem(this.page.storeKey);
    this.editor.text = Codes.DEFAULT;
  };

  private onEditChange = function(event: any) {
    // console.log(event);
    this.saveCode(event);
  };

  private runScript = function() {
    this.log.text = "";
    this.log.result = "";
    let start = new Date();
    let copyCode = this.editor.text;
    // prepare return console.log
    let myConsoleLog = "console.returnLog = function(callback, arg1, arg2, arg3, arg4) { if(arg4) { console.log(arg1, arg2, arg3, arg4); callback(arg1 + ' ' + arg2 + ' ' + arg3 + ' ' + arg4); } else if(arg3) { console.log(arg1, arg2, arg3); callback(arg1 + ' ' + arg2 + ' ' + arg3); } else if(arg2) { console.log(arg1, arg2); callback(arg1 + ' ' + arg2); } else if(arg1) { console.log(arg1); callback(arg1); } }\n";
    copyCode = copyCode.replace(/console.log\(/g, "console.returnLog(callback, ");
    copyCode = myConsoleLog + copyCode;
    console.log(copyCode);
    let nf = new Function("cb", copyCode);
    this.log.result = "&#62;&#62;&nbsp;" + nf((log) => {
      this.log.text += "&#62;&nbsp;" + log + "<br>";
    });
    let end = new Date();
    this.log.duration = end.getTime() - start.getTime();
  };

  private download = function() {
    var blob = new Blob([this.editor.text], {type: "application/javascript;charset=utf-8"});
    saveAs(blob, (this.question ? this.question.title : "js_test") + ".js");
  };

}



