import { Injectable } from '@angular/core';
import { Question } from '../../modules/question';
import { Questions } from '../../resources/questions';
import { Codes } from '../../resources/codes'

@Injectable()
export class QuestionService {

  private questions: Array<Question>;

  constructor() {
    this.questions = Questions.ALL;
  }

  private requestQuestionList = function(success) {
    let list = [];
    for(var o in this.questions) {
      let q = this.questions[o];
      list.push({
        title: q.title,
        difficulty: q.difficulty
      });
    }
    success(list);
  };

  private requestQuestion = function(title: string, success) {
    let question = null;
    for(var o in this.questions) {
      let q = this.questions[o];
      if(q.title == title) {
        question = q;
        break;
      }
    }
    success(question)
  };

  private requestAnswer = function(hint: string, success) {
    success(Codes[hint]);
  };

}



